.overlay-backdrop {
  border: 1px solid black;
  position:fixed;
  top:0px;
  left:0px;
  width: 100vw;
  height:100vh;
  opacity: 0.5;
  background-color: black;
  z-index: 101;
}

.wrapper{
  position:fixed;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  background-color: white;
  z-index: 102;
}